import Vue from 'vue'
import Router from 'vue-router'

import posMain from '@/components/page/posMain'
// @ 是wenpack规定 @代表src目录
// 后缀名写不写都可以 .vue .js
// import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

export default new Router({
 routes:[
 	{
 		path: '/',
 		name: 'posMain',
 		component: posMain
 	}
 ]
})



