<template>
	<div class="pos">
		<el-row>
			<el-col :span="10" class="posOrder" id="seth">
				<el-tabs value="first">
					<el-tab-pane label="点餐" name="first">
						<el-table :data="tableData" style="width:100%;" border>
							<el-table-column prop="name" label="商品"></el-table-column>
							<el-table-column prop="count" label="数量"></el-table-column>
							<el-table-column prop="price" label="单价"></el-table-column>
							<el-table-column label="操作" fixed="right">
								<template slot-scope="scope">
									<el-button type="text" size="small" @click="delSingleGoods(scope.row)">删除</el-button>
									<el-button type="text" size="small" @click="addOrderList(scope.row)">增加</el-button>
								</template>
							</el-table-column>
						</el-table>
						<div class="total">
							<small>数量：</small>{{totalCount}}个<small>金额：</small>{{totalMoney}}元
						</div>
						<div class="btn">
							<el-button size="medium" type="primary" round @click="delAll()">删除</el-button>
							<el-button size="medium" type="success" round>挂单</el-button>
							<el-button size="medium" type="info" round @click="checkOut()">结账</el-button>
						</div>
					</el-tab-pane>
					<el-tab-pane label="挂单" name="second"></el-tab-pane>
					<el-tab-pane label="外卖" name="third"></el-tab-pane>
				</el-tabs>
			</el-col>
			<el-col :span="14">
				<div>
					<div class="title">
						<span>热卖推荐</span>
					</div>
					<div class="hot">
						<ul>
							<li v-for="goods in hotGoodsList" @click="addOrderList(goods)">
								<span>{{goods.goodsName}}</span>
								<span>￥{{goods.price}}</span>
							</li>
						</ul>
					</div>
				</div>
				<div class="goodsType">
					<el-tabs>
						<el-tab-pane label="主食">
							<ul class="cookList">
								<li v-for="abc in type0Goods" @click="addOrderList(abc)">
									<span class="foodImg"><img :src='abc.goodsImg' width="100%"></span>
									<span class="foodName">{{abc.goodsName}}</span>
									<span class="foodPrice">￥{{abc.price}}</span>
								</li>
							</ul>
						</el-tab-pane>
						<el-tab-pane label="零食">
							<ul class="cookList">
								<li v-for="abc in type1Goods" @click="addOrderList(abc)">
									<span class="foodImg"><img :src='abc.goodsImg' width="100%"></span>
									<span class="foodName">{{abc.goodsName}}</span>
									<span class="foodPrice">￥{{abc.price}}</span>
								</li>
							</ul>
						</el-tab-pane>
						<el-tab-pane label="饮料">
							<ul class="cookList">
								<li v-for="abc in type2Goods" @click="addOrderList(abc)">
									<span class="foodImg"><img :src='abc.goodsImg' width="100%"></span>
									<span class="foodName">{{abc.goodsName}}</span>
									<span class="foodPrice">￥{{abc.price}}</span>
								</li>
							</ul>
						</el-tab-pane>
						<el-tab-pane label="套餐">
							<ul class="cookList">
								<li v-for="abc in type3Goods" @click="addOrderList(abc)">
									<span class="foodImg"><img :src='abc.goodsImg' width="100%"></span>
									<span class="foodName">{{abc.goodsName}}</span>
									<span class="foodPrice">￥{{abc.price}}</span>
								</li>
							</ul>
						</el-tab-pane>
					</el-tabs>
				</div>
			</el-col>
		</el-row>
	</div>
</template>
<script type="text/javascript">
import axios from 'axios'
export default {
 	name:'pos',
 	data(){
 		return {
 			tableData:[],
 			hotGoodsList:[],
 			type0Goods:[],
 			type1Goods:[],
 			type2Goods:[],
 			type3Goods:[],
 			totalMoney:0,
 			totalCount:0
 		};
 	},
 	methods: {
 		addOrderList(goods){
 			//每次总件数 总金额清零
 			 this.totalCount = 0;
 			 this.totalMoney = 0;
 			//第一次点击添加到点餐列表
 			//第二次点击数量增加
 			let isHave=false;
 			for(let i=0;i<this.tableData.length;i++){
 				if(this.tableData[i].goodsId==goods.goodsId){
 					isHave = true;
 				}
 			};

 			if(isHave){
 				//存在商品
 				let arr = this.tableData.filter(val=>val.goodsId == goods.goodsId);
 				arr[0].count++;
 			}else{
 				//不存在商品
 				let newGoods = {
 					goodsId:goods.goodsId,
 					name:goods.goodsName,
 					price:goods.price,
 					count:1
 				};
 				this.tableData.push(newGoods);
 			};

 			//计算总件数 总金额
 			this.getAll();
 		},
	 		//删除单件商品
	 		delSingleGoods(goods){
	 			this.tableData=this.tableData.filter(ele=>ele.goodsId!=goods.goodsId);
	 			this.getAll();
	 			
 		},
 		getAll(){
 				//获得总数
 				this.totalCount=0;
 				this.totalMoney=0;
 				this.tableData.forEach(ele=>{
 				this.totalCount+=ele.count;
 				this.totalMoney=this.totalMoney+(ele.count*ele.price)
 			})
 		},
 		delAll(){
 				//删除全部
 				this.tableData=[];
 				this.totalCount=0;
 				this.totalMoney=0;
 		},
 		checkOut(){
 				//结账按钮
 				if(this.totalCount){
 					this.$message('结账成功！感谢你为本店做了一份贡献！');
 					this.delAll();
 				}else{
 					this.$message('别着急！顾客还没下单！');
 				}
 		}
 	},
 	created:function () {
 		//热卖推荐
 		axios.get('http://jspang.com/DemoApi/oftenGoods.php')
 		.then(res=>{
 			this.hotGoodsList = res.data;
 		})
 		.catch(err=>{
 			alert('出现错误！');
 		})
 		//商品分类
 		axios.get('http://jspang.com/DemoApi/typeGoods.php')
 		.then(res=>{
 			this.type0Goods=res.data[0];
 			this.type1Goods=res.data[1];
 			this.type2Goods=res.data[2];
 			this.type3Goods=res.data[3];
 		})
 		.catch(err=>{
 			alert('网络错误！')
 		})
 	},
 	mounted:function () {
 		var H = document.body.offsetHeight;
 		document.getElementById("seth").style.height = H + 'px';
 	}
}
</script>

<style scope>
.posOrder{
	background-color:#F9FAFC;
	border-right:1px solid #c0ccda;
}
.btn{
	margin-top:35px;
	margin-left:150px;
}
.title{
	width:100%;
	height:38px;
	font-size:18px;
	border-bottom:1px solid #c0ccda;
	text-align:left;
	text-indent:25px;
	line-height:38px;
}
.hot li{
	list-style: none;
    float:left;
    border:1px solid #E5E9F2;
    border-radius:10px;
    padding:10px;
    margin:10px;
    background-color:#fff;
    transition:1s;
    cursor:pointer;
}
.hot li:hover{
	color:#fff;
	background-color:#58B7FF;
}
.goodsType{
	clear:both;
}
.cookList li{
	overflow:hidden;
	float:left;
	width:25%;
	height:auto;
	border:1px solid #E5E9F2;
	padding:10px;
	margin:10px;
	background-color:#fff;
	cursor:pointer;
}
.cookList li span{
	display:block;
	float:left;
}
.foodImg{
	width:40%;
}
.foodName{
	font-size: 16px;
    padding-left: 10px;
    color:brown;
}
 .foodPrice{
       font-size: 16px;
       padding-left: 10px;
       padding-top:10px;
}
.total{
	width:100%;
	height:auto;
	padding:10px;
	text-align:center;
	border-bottom:1px solid #E5E9F2;
}
.total small:nth-of-type(2){
	padding-left:10px;
}
.el-tabs__nav{
	margin-left:2rem;
}
.btn{
	width:240px;
	height:auto;
	margin:15px auto 0px;
}
</style>


